import React, { Suspense } from 'react';
import './App.css';
import Nav from './components/Nav/Nav';
import {Route, withRouter, BrowserRouter} from "react-router-dom";
import Music from './components/Music/Music';
//import News from './components/News/News';
//import Setings from './components/Setings/Setings';
import DialogsContainer from './components/Dialogs/DialogsContainer';
import UsersContainer from './components/Users/UsersContainer';
import ProfileContainer from './components/Profile/ProfileContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import { connect, Provider } from 'react-redux';
import { compose } from 'redux';
import { initializeApp } from './redux/appReduser';
import Preloader from "./components/Users/preloader.svg"
import store from './redux/redux';
import { withSuspens } from './components/Hoc/withSuspens';
const Setings = React.lazy(() => import('./components/Setings/Setings'))
const News = React.lazy(() => import('./components/News/News'))

 class App extends React.Component {
  componentDidMount () {
    this.props.initializeApp();
  }
  render(){
    if (!this.props.initialized){
      return <img src={Preloader}/>
    }
    let SomeComponent = () => <ProfileContainer store={this.props.store} />
    let SomeComponentDi = () => <DialogsContainer store={this.props.store} />
    let SomeComponentUsers = () => <UsersContainer store={this.props.store} />
    return (
        <div className="app-wrapper">
          <HeaderContainer />
          <Nav />
          <div className="app-wrapper-content">
            {/* <Route path="/Profile" component={Profile}/>
            <Route path="/Dialogs" component={Dialogs}/> */}
            <Route path="/News" render={withSuspens(News)}/>
            <Route path="/Music" component={Music}/>
            <Route path="/Users" render={SomeComponentUsers}/>
            <Route path="/Setings" render={() =>{ return <Suspense fallback={<div>Loading...</div>} ><Setings/></Suspense> }}/>
            <Route path="/Login" render={() => <Login/>} />
            <Route path="/Profile/:userId?" render={SomeComponent} />
            <Route path="/Dialogs" render={SomeComponentDi}/>
          </div>
        </div> 
    );
  }
}



const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

let AppContainer =  compose(withRouter, connect (mapStateToProps , {initializeApp})) (App);

const DimonApp = (props) => {
      return   <BrowserRouter>
                  <Provider store={store}>
                    <AppContainer />
                  </Provider>
               </BrowserRouter>
}

export default DimonApp