import React from 'react';
import {connect} from "react-redux"
import { followSuccess, unfollowSuccess,  setCurrentPageAC ,getUsersThunkCreator} from '../../redux/usersReduser';
import UsersNew from './UsersNew';
import Preloader from "./preloader.svg"
import { compose } from 'redux';
import { getPageSize, getAllUsers, getTotalUsersCount,getCurrentPage , getIsFetching, getFollowingInProgress } from '../../redux/usersSelectors';

class UsersContainer extends React.Component {
    componentDidMount () {
        this.props.getUsers(this.props.currentPage ,this.props.pageSize);
    }

    onPageChanged = (pageNumber) => {
        this.props.getUsers(pageNumber,this.props.pageSize);
        this.props.setCurrentPage(pageNumber);
    }

    render(){
        return <>
            {this.props.isFetching ? <img src={Preloader}/> : null}
                <UsersNew totalUsersCount={this.props.totalUsersCount} pageSize={this.props.pageSize} 
                currentPage={this.props.currentPage}  onPageChanged={this.onPageChanged} users={this.props.users}
                follow={this.props.follow} unfollow={this.props.unfollow}
                toggleIsFollowingProgress={this.props.toggleIsFollowingProgress} followingInProgress={this.props.followingInProgress}/>
                </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getAllUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state) ,
        currentPage: getCurrentPage(state) ,
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

export default compose(connect(mapStateToProps, { 
    follow : followSuccess,
    unfollow : unfollowSuccess,
    setCurrentPage : setCurrentPageAC,
    getUsers: getUsersThunkCreator
    }))(UsersContainer)