import Yoda from "./yoda.jpg"
import Sasha from "./sasha.jpg"
import Tor from "./tor.jpg"
import { getUsers, usersAPI } from "../api/api";
import { updateObjectInArray } from "../utilits/validators/objectsHelpers";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE"
const SET_TOTAL_USERS_COUNTER = "SET_TOTAL_USERS_COUNTER"
const TOGGLE_IS_FETGING = "TOGGLE_IS_FETGING"
const TOGGLE_IS_FOLLOWING_PROGRESS = "TOGGLE_IS_FOLLOWING_PROGRESS"

let initialState = {
    users : [
            //  {id:1, photos:{small: Yoda},followed:false, name: "Dmitriy", status: "I am a boss...", location: {city: "Kharkiv" , country: "Ukraine"}},
            //  {id:2, photos:{small: Tor},followed:true, name: "Andrey", status: "I am a boss too...", location: {city: "Moscow" , country: "Russia"}},
            //  {id:3, photos:{small: Sasha},followed:false, name: "Sasha", status: "I am a looser...", location: {city: "Minsk" , country: "Belarus"}},
] ,
pageSize: 100,
totalUsersCount: 0,
currentPage : 1 ,
isFetching: true ,
followingInProgress: []
}



const usersReduser = (state = initialState,action) => {
    switch (action.type){
        case FOLLOW :
            return  {...state, 
             users : updateObjectInArray(state.users, action.userId, "id" , {followed: true})
            }  
        case UNFOLLOW : 
        return  {...state, 
            users : updateObjectInArray(state.users, action.userId, "id" , {followed: false})
           }  
        case SET_USERS :{ 
         return {...state, users: action.users} 
        } 
        case SET_CURRENT_PAGE : {
            return {...state, currentPage: action.currentPage}
        }
        case SET_TOTAL_USERS_COUNTER : {
            return {...state, totalUsersCount: action.count}
        }
        case TOGGLE_IS_FETGING : {
            return {...state,isFetching: action.isFetching}
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS : {
            return {...state,
                followingInProgress: action.isFetching 
                ? [...state.followingInProgress, action.userId] 
                : state.followingInProgress.filter(id => id != action.userId)}
        }
        default :
            return state;
    }
}


export const followSuccess = (userId) => ({type: FOLLOW , userId})

export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId})

export const usersAC = (users) => ({type: SET_USERS, users})

export const setCurrentPageAC = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})

export const setTotalUsersCounterAC = (totalUsersCount) => ({type:SET_TOTAL_USERS_COUNTER, count:totalUsersCount})

export const toggleIsFetchingAC = (isFetching) => ({type: TOGGLE_IS_FETGING, isFetching})

export const toggleIsFollowingProgressAC = (isFetching, userId) => ({type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching ,userId})

export const getUsersThunkCreator = (currentPage , pageSize) =>{ 
    return async (dispatch) => {
    dispatch(toggleIsFetchingAC(true));
    let data = await getUsers(currentPage, pageSize)
                     dispatch(toggleIsFetchingAC(false));
                     dispatch(usersAC(data.items));
                     dispatch(setTotalUsersCounterAC(data.totalCount))
    } 
}

const followUnfollowFlow = async (dispatch, userId, apiMethod , actionCreator) => {
    dispatch(toggleIsFollowingProgressAC(true, userId));
        let response = await apiMethod(userId)
            if (response.data.resultCode == 0) {
                    dispatch(actionCreator(userId))
            }
        dispatch(toggleIsFollowingProgressAC(false, userId))
}

export const follow = (userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId , usersAPI.follow.bind(usersAPI), followSuccess)
    }
}

export const unfollow = (userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId , usersAPI.unfollow.bind(usersAPI) , unfollowSuccess)
    }
}

export default usersReduser