const SEND_MESSAGE = "SEND_MESSAGE";


let initialState = {
    messagesPage : { messagesData: [{id:1,text:"How are You ?"},{id:2,text:"Hello!"},{id:3,text:"Yo"}],
        dialogsData: [{id:1,name:"Dima"},{ id:2 , name:"Andrey"},{id:3,name:"Alexey"},{id:4 ,name:"Nikita"},{id:5 ,name:"Lily"},{id:6 ,name:"Kirill"}]}
}

const messageReduser = (state = initialState,action) => {
    switch (action.type){
        case SEND_MESSAGE :{
            let stateCopy = {...state}
            let body = action.newMessageBody;
            stateCopy.messagesPage.messagesData.push({id:7 , text:body});
            return stateCopy;
        }
        default :
            return state;
    }
}


export const sendMessageCreator = (newMessageBody) => ({type: SEND_MESSAGE ,newMessageBody})

export default messageReduser