import React from 'react';
import style from "./MyPosts.module.css";
import Post from "./Post/Post"
import { Field, reduxForm } from 'redux-form';
import { required, minLength2, maxLengthCreator } from '../../../utilits/validators/validators';
import { Textarea } from '../../../common/FormsControls/FormsControls';

// const postData = [{id:1 , message:"Катя,ты здесь?", counts:33},{id:2 ,message:"Как дела?", counts:7},{id:3 ,message:"Я скучаю ♥" , counts:100}]

const MyPosts = (props) => {
    
    let onAddPost = (values) => {
        props.addPost(values.postsForm);
        // newPostElement.current.value = " ";
    }

    let postsElements = [...props.postData].reverse().map(item => (<Post id={item.id} message={item.message} counts={item.counts}/>))

    return  <div className="posts">
                <div className={style.postsBlock}>
                    <h3>My posts</h3>
                </div>
                <MyPostsFormRedux onSubmit={onAddPost}/>
                {postsElements}
                {/* <Post message="Как дела?" counts="7"/>
                <Post message="Я скучаю ♥"  counts="100"/>
                <Post />
                <Post />
                <Post />
                <Post />
                <Post /> */}
            </div>           
}

const maxLength20 = maxLengthCreator(20)

const MyPostsForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} name={"postsForm"} placeholder="Написать сообщение"  validate={[required , maxLength20 , minLength2]}/>
            </div>
            <div>
                <button>Add post</button>
                <button>Remove</button>
            </div>
        </form>
    )
}

const MyPostsFormRedux = reduxForm({form: "postsForm"})(MyPostsForm)

export default React.memo(MyPosts);