import React from 'react';
import style from "./Dialogs.module.css";
import DialogsItem from './DialogsItem/DialogsItem';
import Messages from './Messages/Messages';
import { Redirect } from 'react-router-dom';
import { Field, reduxForm } from 'redux-form';
import { Textarea } from '../../common/FormsControls/FormsControls';
import { required, minLength2, maxLengthCreator } from '../../utilits/validators/validators';

const Dialogs = (props) => {

    let state = props.messagesPage; 

    let addNewMessage = (values) => {
        props.sendMessage(values.newMessageBody)
    };

    if (!props.isAuth) {
        return <Redirect to={"/Login"}/>
    }
    
    return <div className={style.dialogs}>
            <div className={style.dialogsItems}>
               {state.messagesPage.dialogsData.map((item) => (<DialogsItem id={item.id}  name={item.name}/>))}
            </div>
            <div className={style.messages}>
                {state.messagesPage.messagesData.map(i=>(<Messages id={i.id} text={i.text}/>))}
                {/* <Messages id="2" text="Hello!"/>
                <Messages id="3" text="Yo"/> */} 
                <AddMessageFormRedux onSubmit={addNewMessage}/>
            </div>
            </div>   
}

const maxLength100 = maxLengthCreator(100);

const AddMessageForm = (props) => {
    return (<form onSubmit={props.handleSubmit}>
                <div>
                    <Field component={Textarea} name="newMessageBody" placeholder="Написать сообщение" validate={[required,minLength2,maxLength100]}/>
                </div>
                <div>
                    <button>Send</button>
                </div>
            </form>)
}

const AddMessageFormRedux = reduxForm({form: "dialogAddMessageForm"})(AddMessageForm)

export default Dialogs;