import React from 'react';
import style from "./Post.module.css"


const Post = (props) =>{
    return <>
                <div id={props.id} className={style.Item}>
                    <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSziCX9HpxmxnZRnhwQyfHeZMFHGn0luZJ24MurgIO4os3PWL_g"></img>
                     {props.message}
                    <div><span>Like</span>{props.counts}</div>
                </div>
        </>
}

export default Post;