import React from 'react';
import style from "./style.module.css"
import Yoda from "./yoda.jpg"
import {NavLink} from "react-router-dom"

const User = (props) => {
    let userNow = props.user;
    return <div>
               <span>
                   <div>
                       <NavLink to={"/Profile/" + userNow.id}>
                       <img className={style.UserImg} src={userNow.photos.small != null ? userNow.photos.small : Yoda} />
                       </NavLink>
                   </div>
                   <div>
                       {userNow.followed ? <button disabled={props.followingInProgress.some(id => id===userNow.id)} onClick={() => {
                           props.unfollow(userNow.id)
                       }
                           }>UnFollow</button> : <button disabled={props.followingInProgress.some(id => id===userNow.id)} onClick={() => {
                             props.follow(userNow.id)
                            }
                               }>Follow</button>} 
                   </div>
               </span>
               <span>
                    <span>
                        <div>{userNow.name}</div>
                        <div>{userNow.status}</div>
                    </span>
                    <span> 
                        <div>{"userNow.location.country"}</div>
                        <div>{"userNow.location.city"}</div>
                    </span>
                </span>          
               </div>
}

export default User